.compares {
  &__item {
    margin-bottom: 40px;

    @include onIpad {
      margin-bottom: 48px;
    }

    @include onDesctop {
      margin-bottom: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.compare {
  &__photo-container {
    display: grid;
    grid-template-columns: (4, 1fr);
    column-gap: 24px;
    margin-bottom: 32px;
  }
  &__photo {
    width: 100%;
    transition: transform 300ms;

    &:hover {
      transform: scale(1.1);
    }
  }

  &__name {
    margin: 0 0 8px;
    @extend %compare-style;
  }

  &__sibtitle {
    margin-bottom: 16px;
  }

  &__price {
    @extend %compare-style;
  }
}
